/* Switch Field */
.switch-field {
  display: flex;
  margin-bottom: 20px;
  overflow: hidden;
}

.switch-field input {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.switch-field input:checked + label {
  background-color: #2a9d8f;
  box-shadow: none;
  color: #ffffff;
}

.switch-field input:checked + label:last-of-type {
  background: #ef476f;
}

.switch-field label {
  background-color: rgba(94, 63, 201, 0.1);
  color: rgba(16, 25, 53, 0.6);
  font-size: 14px;
  line-height: 1;
  text-align: center;
  padding: 8px 16px;
  margin-right: -1px;
  border: 1px solid rgba(16, 25, 53, 0.2);
  transition: all 0.1s ease-in-out;
  font-weight: 700;
  width: 50%;
}

.switch-field label:hover {
  cursor: pointer;
}

.switch-field label:first-of-type {
  border-radius: 4px 0 0 4px;
}

.switch-field label:last-of-type {
  border-radius: 0 4px 4px 0;
}

.switch-field.same-type input:checked + label:last-of-type {
  background: #5e3fc9;
}

.switch-field.same-type label {
  padding: 13px 16px;
  border: 2px solid rgba(16, 25, 53, 0.2);
}

/* Image Preview */
.wrap-custom-file {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 150px;
  text-align: center;
  border: 1px dashed #d2d3d8;
  border-radius: 8px;
}

.wrap-custom-file input[type="file"] {
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 2px;
  overflow: hidden;
  opacity: 0;
}

.wrap-custom-file label {
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
  cursor: pointer;
  border-radius: 8px;
  transition: transform 0.4s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  transition: transform 0.4s;
}

.wrap-custom-file label span {
  display: block;
  color: #101935;
  font-size: 14px;
  transition: color 0.4s;
}

.wrap-custom-file label .upload-icon {
  width: 40px;
  margin: 0 auto;
  margin-bottom: 15px;
}

.wrap-custom-file label.file-ok {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}

.wrap-custom-file label.file-ok span {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0.3rem;
  font-size: 14px;
  font-weight: 700;
  color: #ffffff;
  background-color: rgba(94, 63, 201, 0.5);
}

.wrap-custom-file label.file-ok .upload-icon {
  display: none;
}

.wrap-custom-file .close {
  background: #EF476F;
  height: 30px;
  width: 30px;
  line-height: 27px;
  border-radius: 50%;
  color: #ffffff;
