  display: none;
}

.site-input-groups .checkbox .check-input + label {
  display: inline-block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 5px;
  color: #101935;
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.site-input-groups .checkbox .check-input + label:last-child {
  margin-bottom: 0;
