.site-input-groups .checkbox .check-input + label::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  border: 2px solid #e5e8f2;
  position: absolute;
  left: 0;
  top: 1px;
  opacity: 1;
}

