  color: #ffffff;
  background: #5e3fc9;
  border: 2px solid #5e3fc9;
  content: "\f00c";
  line-height: 18px;
  font-family: "FontAwesome";
  font-weight: normal;
  font-size: 12px;
  text-align: center;
}

