  margin-bottom: 50px;
  position: relative;
  display: flex;
  justify-content: center;
}

.hv-wrapper .hv-item .hv-item-parent:after {
  position: absolute;
  content: "";
  width: 2px;
  height: 25px;
  bottom: 0;
  left: 50%;
  background-color: #7c7474b3;
  transform: translateY(100%);
}

.hv-wrapper .hv-item .hv-item-children {
  display: flex;
  justify-content: center;
}

