/* All notifications */
.notification-list {
  margin-bottom: 20px;
}

.notification-list .single-list {
  background: rgba(21, 29, 48, 0.1);
  padding: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  cursor: default;
  transition: 0.2s;
}

@media (max-width: 575px) {
  .notification-list .single-list {
    display: block;
  }
}

.notification-list .single-list .cont {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 80%;
}

@media (max-width: 575px) {
  .notification-list .single-list .cont {
    align-items: start;
    width: 100%;
  }
  .notification-list .single-list .link {
